import withRoot from './modules/withRoot';
// --- Post bootstrap -----
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';
import { Form, FormSpy } from 'react-final-form';
import Typography from './modules/components/Typography';
import AppAppBar from './modules/views/AppAppBar';
import AppForm from './modules/views/AppForm';
import FormButton from './modules/form/FormButton';
import FormFeedback from './modules/form/FormFeedback';
import TextField from '@material-ui/core/TextField';

const useStyles = makeStyles(theme => ({
  form: {
    marginTop: theme.spacing(6),
  },
  button: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(2),
  },
  feedback: {
    marginTop: theme.spacing(2),
  },
}));

function SignUp() {
  const _OK = 200;
  const _WRONGPWD = 211;
  const _WRONGUSER = 212;

  const classes = useStyles();

  const [myid, setId] = React.useState('');
  const [mypassword, setPasswd] = React.useState('');
  const [mypasswordcon, setPasswdcon] = React.useState('');

  const [status, setStatus] = React.useState('');

  const handleChange = event => {
    setId(event.target.value);
  }
  const handleChange2 = event => {
    setPasswd(event.target.value);
  }
  const handleChange3 = event => {
    setPasswdcon(event.target.value);
  }


  const handleSubmit = () => {
    console.log(myid);
    console.log(mypassword);
    console.log(status);
    //console.log(event.target.validate);

    //console.log(values.email);
  };

  window.location.href = (status === _OK) ? '#/sign_in' : '#/sign_up ';


  return (
    <React.Fragment>
      <AppAppBar />
      <AppForm>
        <React.Fragment>
          <Typography variant="h4" gutterBottom marked="center" align="center">
            Sign Up
          </Typography>
          <Typography variant="body2" align="center">
            <Link href="#/sign_in/" underline="always">
              Already have an account?
            </Link>
          </Typography>
        </React.Fragment>
        <Form onSubmit={handleSubmit} subscription={{ submitting: true }} >
          {({ handleSubmit, submitting }) => (
            <form onSubmit={handleSubmit} className={classes.form} noValidate>
              <TextField
                autoFocus
                //component={RFTextField}
                disabled={submitting || status === _OK}
                // autoComplete="id"
                fullWidth
                label="ID"
                name="id"
                margin="normal"
                required
                value={myid}
                onChange={handleChange}
                helperText={status === _WRONGUSER ? '用户已存在' : ''}
                error={status === _WRONGUSER}

              />
              {/* <Field
                autoComplete="email"
                component={RFTextField}
                disabled={submitting || sent}
                fullWidth
                label="Email"
                margin="normal"
                name="email"
                required
              /> */}
              <TextField
                fullWidth
                //component={RFTextField}
                disabled={submitting || status === _OK}
                required
                name="password"
                // autoComplete="current-password"
                label="Password"
                type="password"
                margin="normal"
                value={mypassword}
                onChange={handleChange2}
                helperText={status === _WRONGPWD ? '密码过于简单，要求8位以上，同时含有数字、字母以及特殊字符' : '要求8位以上，同时含有数字、字母以及特殊字符'}
                error={status === _WRONGPWD}
              />
              <TextField
                fullWidth
                //component={RFTextField}
                disabled={submitting || status === _OK}
                required
                name="password"
                // autoComplete="current-password"
                label="Confirming Password"
                type="password"
                margin="normal"
                value={mypasswordcon}
                onChange={handleChange3}
                helperText={mypasswordcon !== '' && !(mypasswordcon === mypassword) ? '两次输入不同' : ''}
                error={mypasswordcon !== '' && !(mypasswordcon === mypassword)}
              />

              <FormSpy subscription={{ submitError: true }}>
                {({ submitError }) =>
                  submitError ? (
                    <FormFeedback className={classes.feedback} error>
                      {submitError}
                    </FormFeedback>
                  ) : null
                }
              </FormSpy>
              <FormButton
                className={classes.button}
                disabled={myid === '' || mypassword === '' || mypasswordcon === '' || mypassword !== mypasswordcon || submitting || status === _OK}
                color="secondary"
                fullWidth
                myid={myid}
                mypassword={mypassword}
                myopit={'signup'}
                status={status}
                setStatus={setStatus}
                // href={status === _OK ? "#/sign_in/" : ""}

              >
                {submitting || status === _OK ? 'In progress' : 'Sign Up'}
              </FormButton>
            </form>
          )}
        </Form>
      </AppForm>
      {/* <AppFooter /> */}
    </React.Fragment>
  );
}

export default withRoot(SignUp);
