import React from 'react';
import PropTypes from 'prop-types';
import defer from './defer';
import Button from '../components/Button';
import request from 'superagent'

global.initdata = [];
const ip_add = `//10.60.102.252:20075`;
// const ip_add = `//192.168.80.230:2000`;
function FormButton(props) {
  const { disabled, mounted, myid, mypassword, myopit, status, setStatus, ...others } = props;
  return <Button disabled={!mounted || disabled} type="submit" variant="contained" {...others}
    onClick={() => {
      request
        .post(ip_add)
        .send(JSON.stringify({  //向后端传的数据
          type: myopit,
          username: myid,
          passwd: mypassword,

        }))
        //.withCredentials()
        .then(res => {
          if (res.ok) {
            console.log('response');
            console.log(res)

            request
              .post(ip_add)
              .send(JSON.stringify({  //向后端传的数据
                type: 'getdir',
                username: myid,
              }))
              //.withCredentials()
              .then(res => {
                if (res.ok) {
                  console.log('response');
                  console.log(res)
                  console.log(res.text)
                  setStatus(res.status)
                  //console.log(JSON.parse(JSON.parse(result.data).data))
                  global.initdata = JSON.parse(res.text);
                }
              })
              .catch(error => {
                console.log(error)
                return error
              })
          }
        })
        .catch(error => {
          console.log(error)
          return error
        })
      
      console.log(myid);
      console.log(mypassword);
      console.log(myopit);

      // if (myopit === 'signup')
      //   window.location.href = (status === 200) ? '#/sign_in' : '#/sign_up ';
      // else
      //   window.location.href = (status === 200) ? '#/sign_up' : '#/admin ';

    }}
  />;
}

FormButton.propTypes = {
  disabled: PropTypes.bool,
  mounted: PropTypes.bool,
};

export default defer(FormButton);
