const ip_add = `//10.60.102.252:20075`;
// const ip_add = `//192.168.80.230:2000`;
function gettable(setData) {
  request
    .post(ip_add)
    .send(JSON.stringify({  //向后端传的数据
      type: 'getdir',
      username: global.userID,
    }))
    //.withCredentials()
    .then(res => {
      if (res.ok) {
        console.log('response');
        console.log(res)
        console.log(res.text)
        //console.log(JSON.parse(JSON.parse(result.data).data))
        setData(JSON.parse(res.text));
      }
    })
    .catch(error => {
      console.log(error)
      return error
    })

};


function action_delete(mydeleteid) {
  request
    .post(ip_add)
    .send(JSON.stringify({  //向后端传的数据
      type: 'delete',
      fileid: mydeleteid,
    }))
    //.withCredentials()
    .then(res => {
      if (res.ok) {
        console.log('response');
        console.log(res)
        console.log(res.text)
        //console.log(JSON.parse(JSON.parse(result.data).data))
        //setData(JSON.parse(res.text));
      }
    })
    .catch(error => {
      console.log(error)
      return error
    })

};
function copy_or_move(type, from_id, to_id) {
  request
    .post(ip_add)
    .send(JSON.stringify({  //向后端传的数据
      type: type,
      from_id: from_id,
      to_id: to_id
    }))
    //.withCredentials()
    .then(res => {
      if (res.ok) {
        console.log('response');
        console.log(res)
        console.log(res.text)
        //console.log(JSON.parse(JSON.parse(result.data).data))
        //setData(JSON.parse(res.text));
      }
    })
    .catch(error => {
      console.log(error)
      return error
    })

};


